html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border: none;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #042323;
  color: #FFF;
  letter-spacing: 0.2px;
}
@media screen and (max-width: 1280px) {
  body.lock {
    overflow: hidden;
  }
}

main {
  margin-top: 104px;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 16px;
}
@media screen and (max-width: 1280px) {
