.banner {
  margin-bottom: 80px;
}
.banner__wrap {
  padding: 190px 0 116px;
}
.banner__heading {
  font-family: "Mulish", sans-serif;
  max-width: 600px;
  font-size: 50px;
  line-height: 1.2;
  margin-bottom: 40px;
}
.banner__text {
  font-family: "Mulish", sans-serif;
  font-size: 20px;
  line-height: 1.2;
  max-width: 50%;
  margin-bottom: 40px;
}
.banner__btn-link {
  display: block;
  width: 154px;
  padding: 8px 0;
  border: 2px solid #e48564;
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
