.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.header__wrap {
  min-height: 104px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo-link {
  display: block;
  margin-right: 16px;
}
.header__logo-img {
  display: block;
  width: 50px;
  height: 60px;
  fill: #ffffff;
  transition: all 0.25s ease 0s;
}
.header__logo-img:hover {
  fill: #e48564;
}
.header__logo-text {
  max-width: 320px;
  font-family: "Mulish", sans-serif;
  font-size: 16px;
}
.header__nav {
  display: flex;
  align-items: center;
  position: relative;
}
.header__menu {
  display: flex;
  margin-right: 26px;
}
.header__nav-item:not(:last-child) {
  margin-right: 24px;
}
.header__nav-link {
  font-family: "Mulish", sans-serif;
  font-size: 16px;
  line-height: 1.7;
  position: relative;
  transition: color 0.25s ease 0s;
}
.header__nav-link::after {
  content: "";
  background: #e48564;
  height: 1px;
  width: 100%;
  position: absolute;
  bottom: -8px;
  left: 0;
  transform: scaleX(0);
  transition: transform 0.25s ease;
}
.header__nav-link:hover {
  color: #e48564;
}
.header__nav-link:hover::after {
  transform: scaleX(1);
}
.header__blog-link {
  display: block;
  width: 154px;
  margin-right: 26px;
  padding: 8px 0;
  border: 2px solid #e48564;
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  background-color: #e48564;
  color: #FFF;
  transition: all 0.2s ease 0s;
}
.header__blog-link:hover {
  background: none;
  color: #e48564;
}
.header__social-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  align-items: center;
}
.header__instagram-icon {
  fill: #FFF;
  transition: fill 0.3s ease;
}
.header__instagram-icon:hover {
  fill: #e48564;
}
.header__telegram-icon {
  fill: #FFF;
  transition: fill 0.3s ease;
}
.header__telegram-icon:hover {
  fill: #e48564;
}
.header__burger-menu {
  display: none;
}
@media screen and (max-width: 1280px) {
  .header {
    margin-bottom: 175px;
  }
  .header__logo-wrap {
    position: relative;
    z-index: 1;
  }
  .header__logo-text {
    margin-right: 16px;
  }
  .header__nav {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    transform: translate(-100%);
    height: 100vh;
    background-color: #042323;
    transition: transform 0.4s ease;
  }
  .header__nav.open {
    transform: translate(0);
  }
  .header__menu {
    display: block;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-right: 0;
    text-align: center;
  }
  .header__nav-item:not(:last-of-type) {
    margin-right: 0;
    margin-bottom: 24px;
  }
  .header__nav-link {
    font-weight: 400;
    font-size: 24px;
    line-height: 1.5;
  }
  .header__blog-link {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .header__social-list {
    position: absolute;
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .header__burger-menu {
    position: relative;
    z-index: 2;
    display: block;
    width: 50px;
    height: 30px;
    border: 2px solid #ffffff;
    border-radius: 10px;
    padding: 5px;
  }
  .header__burger-menu.open {
    width: 150px;
  }
}
@media screen and (max-width: 768px) {
  .header {
    margin-bottom: 0;
  }
  .header__wrap {
    min-height: 80px;
  }
  .header__logo-text {
    text-align: center;
    font-size: 12px;
  }
  .header__logo-text.open {
    text-align: start;
  }
  .header__menu {
    top: 50%;
  }
  .header__blog-link {
    top: 80%;
  }
  .header__social-list {
    top: 90%;
  }
  .header__burger-menu.open {
    width: 200px;
  }
}

.modal {
  display: none;
  position: fixed;
  padding-top: 70px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 500;
}
.modal__content {
  margin: auto;
  display: block;
  max-height: 80vh;
  animation-name: zoom;
  animation-duration: 0.6s;
}
@keyframes zoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
@media screen and (max-width: 768px) {
  .modal__content {
    max-width: 300px;
  }
}
.modal__close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #ffffff;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}
.modal__close:hover, .modal__close:focus {
  color: #bdbdbd;
  text-decoration: none;
  cursor: pointer;
}

